<template>
    <Dropdown
        v-model="lang"
        class="ignore-opencc"
        :options="languages"
        option-label="name"
        style="z-index: 10000"
        option-value="code"
    />
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "Localize",
    data() {
        return {
            // lang: "zh-HK",
            languages: [
                { name: "", code: "zh-HK" },
                { name: "", code: "zh-CN" },
            ],
        };
    },
    computed: {
        ...mapGetters(["getLang"]),
        lang: {
            get() {
                return this.getLang;
            },
            set(lang) {
                return this.setLang(lang);
            },
        },
    },
    watch: {
        lang() {
            console.log(this.getLang);
            this.localizePage();
        },
    },
    methods: {
        ...mapActions(["setLang", "localizePage"]),
    },
};
</script>
